const mapStateToProps = (state) => ({
    data : state.slotTime.data,
    status:state.slotTime.status
})

const mapDispatchToProps = dispatch => {
    return{
        getSlotTime: () => dispatch(getSlotTime())
    }
}


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home);
